import fetch from "isomorphic-unfetch";
import PageTitle from "../components/PageTitle";
import "../index.css";

const Index = ({ posts }) => (
  <>
    <PageTitle>Kategorien</PageTitle>
    <p className="text-blue-600">Add some description here</p>

    <ul className="p-4 shadow bg-grey rounded list-disc">
      {posts.map(({ id, title }) => (
        <li key={id}>{title}</li>
      ))}
    </ul>
  </>
);

Index.getInitialProps = async function() {
  const fetchPosts = await fetch("https://jsonplaceholder.typicode.com/posts");
  const posts = await fetchPosts.json();

  return {
    posts
  };
};

export default Index;
